    }
    p .parrafo {
        margin: auto 5vh;
    }
}
@media (max-width: 940px) {
    .imgMain {
        height: 35vh;
    }
    .username {
        font-size: 2vw;
    }
    .title {
        padding: 2vh 0vh;
        margin: auto 5vh;
    }
    
    .subtitle {
        margin: auto 5vh;
    }
}

@media (max-width: 450px) {
    div .imgMain {
        height: 20vh;
    }
    .username {
        font-size: 4vw;
    }
}

  @media (max-width: 1400px) {
    div .aGame {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    /* div .imgFluid {
        margin: 1vh auto;
    } */
