}

#categoryNotFound {
    opacity: 90%;
    background-position: top center;
    background-size: contain;
    background-repeat: no-repeat;
    min-height: 65vh;
    width: 60vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: wrap;
    box-shadow: 7px 7px 7px #252322;
}


#favConsoleContainer {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.favConsole{
